import React, { useContext, useState, useRef, useEffect } from "react";
import { useHistory } from "react-router";

import { ArrowDropDown, Notifications, Search } from "@material-ui/icons";

import { AuthContext } from "../../context/authContext/AuthContext";

import "./Header.scss";
import { Link } from "react-router-dom";
import $ from "jquery";
const Header = () => {
  const history = useHistory();
  const { dispatch, user } = useContext(AuthContext);
  const [searchInput, setSearchInput] = useState();
  const [isScrolled, setIsScrolled] = useState(false);
  window.onscroll = () => {
    setIsScrolled(window.pageYOffset === 0 ? false : true);
  };
  const logoutHandler = () => {
    dispatch({ type: "LOGOUT" });
    history.replace("/register");
  };
  useEffect(() => {
    $(document).ready(function () {
      $(".searchbar-icon").click(function () {
        $(".input-searchbar-wrapper").addClass("active");
        $(".searchbar-input").focus();
        $(this).addClass("inactive");
      });
      $(".searchbar-input").blur(function () {
        $(".input-searchbar-wrapper").removeClass("active");
        setTimeout(() => {
          $(".searchbar-icon").removeClass("inactive");
        }, 100);
      });
    });
  });
  const onSearchSubmitHandler = (event) => {
    event.preventDefault();
    history.push(`/search?q=${searchInput}`);
  };
  return (
    <nav className={isScrolled ? "header scrolled" : "header"}>
      <div className="left">
        <Link to="/">
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/2560px-Netflix_2015_logo.svg.png"
            alt=""
          />
        </Link>
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/series">TV Shows</Link>
          </li>
          <li>
            <Link to="/movie">Movies</Link>
          </li>
          <li>
            <Link to="/">My List</Link>
          </li>
        </ul>
      </div>
      <div className="right">
        <form
          className="input-searchbar-wrapper"
          onSubmit={onSearchSubmitHandler}
        >
          <Search className="searchbar-icon" />
          <input
            type="text"
            className="searchbar-input"
            placeholder="Movie, TV Series"
            autoFocus
            onChange={(e) => setSearchInput(e.target.value)}
          />
        </form>
        <Notifications className="icon" />
        <img
          src={
            user.profileImg ||
            "data:image/png;base64,"
          }
          atl=""
        />
        <div className="profile">
          <ArrowDropDown className="dropdown-icon" />
          <div className="dropdown-block">
            <Link to="/YourAccount" className="link">
              {" "}
              <p>Setting</p>
            </Link>{" "}
            <p onClick={logoutHandler}>Logout</p>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Header;
