import React, { useContext, useState, useRef, useEffect } from "react";
import { useHistory } from "react-router";

import {
  ArrowDropDown,
  ArrowDropUp,
  Notifications,
  Search,
} from "@material-ui/icons";

import { AuthContext } from "../../context/authContext/AuthContext";

import "./SettingHeader.scss";
import { Link } from "react-router-dom";
import $ from "jquery";
const SettingHeader = () => {
  const history = useHistory();
  const { dispatch, user } = useContext(AuthContext);
  const [searchInput, setSearchInput] = useState();
  const [isScrolled, setIsScrolled] = useState(false);
  window.onscroll = () => {
    setIsScrolled(window.pageYOffset === 0 ? false : true);
  };
  const logoutHandler = () => {
    dispatch({ type: "LOGOUT" });
    history.replace("/register");
  };
  useEffect(() => {
    $(document).ready(function () {
      $(".settingHeader > .right > .img-wrapper").hover(
        function () {
          $(".settingHeader > .right > .profile").addClass("active");
        },
        function () {
          $(".settingHeader > .right > .profile").removeClass("active");
        }
      );
      $(".settingHeader > .right > .profile").hover(
        function () {
          $(this).addClass("active");
        },
        function () {
          $(this).removeClass("active");
        }
      );
    });
  });

  return (
    <nav className="settingHeader">
      <div className="left">
        <Link to="/">
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/2560px-Netflix_2015_logo.svg.png"
            alt=""
          />
        </Link>
      </div>
      <div className="right">
        <div className="img-wrapper">
          <img
            src={
              user.profileImg ||
              "data:image/png;base64,"
            }
            atl=""
          />{" "}
          <ArrowDropDown className="dropdown-icon" />
        </div>
        <div className="profile">
          <div className="dropdown-block">
            <p onClick={logoutHandler}>Logout</p>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default SettingHeader;
